import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Tramite } from 'src/app/model/tramite/tramite.model';
import { Usuario } from 'src/app/model/usuario/usuario.model';
import { TramitesService } from 'src/app/services/tramites.service';
import Swal from 'sweetalert2' ;

@Component({
  selector: 'app-registro',
  templateUrl: './registro.component.html',
  styles: [
  ]
})
export class RegistroComponent implements OnInit {
  forma:FormGroup;
  archivoTramite:File;

  constructor(
  
    public router:Router,
    public _tramiteService:TramitesService

  ) { }

  seleccionImage(archivo:File){
    
    console.log(archivo);
    this.archivoTramite=archivo;

   }

  ngOnInit(): void {

    
    this.forma=new FormGroup({
      
      nombreTramite: new FormControl(null, Validators.required),
      
      nombreUsuario: new FormControl(null, Validators.required),
      apellidoUsuario: new FormControl(null, Validators.required),
      fechaTramite: new FormControl(null, Validators.required),
      //archivoTramite: new FormControl(null, Validators.required),
      areaDestino: new FormControl(null, Validators.required),
    });
  }

 

  registrarTramite(){

      let tramite = new Tramite(
        0,
      this.forma.value.nombreTramite,
      
      this.forma.value.nombreUsuario,
      
      this.forma.value.apellidoUsuario,
      
      this.forma.value.fechaTramite,
      
      this.forma.value.archivoTramite,
      
      this.forma.value.areaDestino,
      
      0,
      
    );
   
    if (this.forma.invalid) {
      console.log('Campos invalidos');
      Swal.fire('Campos no Válidos');
      return;
    }

    this._tramiteService.guardarTramites(tramite, this.archivoTramite)
        .then(resp=>{
          console.log(resp);
          Swal.fire('Registrado Exitosamente');
          this.router.navigate(['/dashboard']);
        })
        .catch(resp=>{
          console.log(resp);
        })                  
  }

  City: any = ['area1']
  
}
