import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import{Area1Service} from '../../../services/areas/area1.service';
import { saveAs} from 'file-saver';
import { jsPDF } from "jspdf";
import { ObservadosService } from 'src/app/services/observados.service';
import { Usuario } from 'src/app/model/usuario/usuario.model';
import { Tramite } from 'src/app/model/tramite/tramite.model';
import Swal from 'sweetalert2' ;
import {UsuarioService} from '../../../services/usuario.service';

@Component({
  selector: 'app-area1',
  templateUrl: './area1.component.html',
  styleUrls: ['./area1.component.css']
})
export class Area1Component implements OnInit {

    Datos:any=[];
    area1:any;
    uploadedImagesObj:any=[];
    pdfSrc = this.Datos;

  constructor(
    public _area1Service:Area1Service, private downloads: FileService, public _observados:ObservadosService,
    public _usuarioService: UsuarioService

  ) { }

  ngOnInit(): void {
    this.enlistarArea1();
  }

  enlistarArea1(){
    this._area1Service.cargarArea1()
        .subscribe(
          res=>{
              this.Datos=res;
              console.log(res);
          }
        );
  }

  download(filename, id:String){
    console.log(id);
    this._area1Service.downloadFile(filename, id)
    .subscribe(
        data =>{ saveAs(data, id)
        console.log(data);
        },
        error => console.error(error)
    );
  }

  actualizarEstado(id:number){
      const t = new Tramite();
      t.estadoTramite=2;
      Swal.fire({
        title: '¿Seguro que desea observar Tramite?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Aceptar'
      }).then((result) => {
        if (result.isConfirmed) {
          this._observados.actualizarEstado(id,t)
              .subscribe(resp=>{
                console.log(resp);
              });
          Swal.fire(
            'Modificado',
            'Ha sido Observado',
            'success'
          )
        }
      });
  }

}
