import { Component, OnInit } from '@angular/core';
import { ObservadosService } from 'src/app/services/service.index';

@Component({
  selector: 'app-observados',
  templateUrl: './observados.component.html',
  styleUrls: ['./observados.component.css']
})
export class ObservadosComponent implements OnInit {

  datosObservados:any=[];

  constructor(
    public _MostrarObservados:ObservadosService
    
  ) { }

  ngOnInit(): void {
    this.cargarDatos();
  }

  cargarDatos(){
    this._MostrarObservados.mostrarObservados()
      .subscribe(
        res=>{
          this.datosObservados=res;
        }
      );
  }
}
