
import { Component } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import { RegisterComponent } from '../login/register.component';
import { AdminGuard, LoginGuardGuard } from '../services/service.index';
import { EnlistarUsuarioComponent } from '../usuarios/enlistar-usuario/enlistar-usuario.component';
import { Area1Component } from './areas/area1/area1.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ObservadosComponent } from './observados/observados.component';

import {PagesComponent} from './pages.component';
import { EnlistarRegistroComponent } from './registro/enlistar-registro/enlistar-registro.component';
import { RegistroComponent } from './registro/registro.component';




const pagesRoutes:Routes=[
    
        {path:'dashboard',component: DashboardComponent},
        {
            path:'area1',
            component:Area1Component,
            canActivate:[AdminGuard]
        },
        {path:'tramite',component:RegistroComponent},
        {path:'tramitelistado',component:EnlistarRegistroComponent},
        {path:'usuariolistado',component:EnlistarUsuarioComponent},
        
        {path:'usuario',component:RegisterComponent},
        {path:'observados',component:ObservadosComponent},
    

];

export const PAGES_ROUTES=RouterModule.forChild(pagesRoutes);