import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/model/usuario/usuario.model';
import { UsuarioService } from 'src/app/services/service.index';
import { Router } from '@angular/router';
import Swal from 'sweetalert2' ;

@Component({
  selector: 'app-enlistar-usuario',
  templateUrl: './enlistar-usuario.component.html',
  styles: [
  ]
})
export class EnlistarUsuarioComponent implements OnInit {

  usuarios:any=[];
  userId:any=null;
  constructor(
    public _usuarioService: UsuarioService,public  router: Router

  ) { }

  ngOnInit(): void {
    this.cargarUsurarios2();
  }

  cargarUsurarios2(){
    this._usuarioService.cargarUsuarios()
      .subscribe(
        res=>{
          this.usuarios=res;
          console.log(res);
        }
      );
  }

  eliminarUsuario2(usuario){    
    Swal.fire({
        title: 'Seguro que desea eliminar a '+ usuario.nombreUsuario,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Aceptar'
    }).then((result) => {
      if (result.isConfirmed) {
        this._usuarioService.eliminarUsuarios(usuario.idUsuario)
            .subscribe(res=>{
              console.log('Eliminado');
              });
          Swal.fire(
            'ELIMINADO',
            'Ha sido Eliminado',
            'success'
          )
          this.router.navigate(['/dashboard'])
      }
    });
  }

}


