
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Usuario } from '../model/usuario/usuario.model';
import { UsuarioService } from '../services/service.index';
import Swal from 'sweetalert2' ;

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./login.component.css']
})
export class RegisterComponent implements OnInit {

  forma:FormGroup;

  constructor(
    public _usuarioService: UsuarioService,
    public router:Router,
    private formBuilder: FormBuilder

  ) { }

  ngOnInit() {
    this.forma=new FormGroup({
      nombreUsuario: new FormControl(null, Validators.required),
      apellidoUsuario: new FormControl(null, Validators.required),
      telefonoUsuario: new FormControl(null, Validators.required),
      areaUsuario: new FormControl(null, Validators.required),
      rolUsuario: new FormControl(null, Validators.required),
      nombre: new FormControl(null, Validators.required),
      email: new FormControl(null, [Validators.required,Validators.email]),
      password: new FormControl(null, Validators.required),
      password2: new FormControl(null, Validators.required),
      condiciones: new FormControl(true),
    });
  }

  registrarUsuario(){
    let usuario = new Usuario(
      '',
      this.forma.value.nombreUsuario,
      this.forma.value.apellidoUsuario,
      this.forma.value.telefonoUsuario,
      this.forma.value.areaUsuario,
      this.forma.value.rolUsuario,
      this.forma.value.nombre,
      this.forma.value.email,
      this.forma.value.password,
      ''
    );
      if (this.forma.invalid) {
        console.log('Campos invalidos');
        Swal.fire('Campos no Válidos');
        return;
      }

    this._usuarioService.crearUsuario(usuario)
        .subscribe(res => {
          console.log(res);
          Swal.fire('Creado Exitosamente');
          this.router.navigate(['/usuariolistado']);
        });                        
  }

  //Combobox
  City: any = ['area1', 'administracion']
  City2: any = ['Administrador', 'Trabajador']
  
}
