import { Component, OnInit } from '@angular/core';

import{TramitesService} from '../../../services/tramites.service';

@Component({
  selector: 'app-enlistar-registro',
  templateUrl: './enlistar-registro.component.html',
  styleUrls: ['./enlistar-registro.component.css']
})
export class EnlistarRegistroComponent implements OnInit {

  tramites:any=[];

  constructor(
    public _tramteService:TramitesService
  ) { }

  ngOnInit(): void {
    this.enlistarUsuario();
  }

  enlistarUsuario(){
    this._tramteService.enlistaTramite()
          .subscribe(
              res=>{
                this.tramites=res;
              }
          );
  }

}
